import { getHideBaseStyleMixin } from '../mixins/act/hide-base-style';
import { getActClass } from './utils';


export default {
  name: 'PressActExplainDialog',
  options: {
    virtualHost: true,
    styleIsolation: 'shared',
  },
  mixins: [getHideBaseStyleMixin()],
  props: {
    show: {
      type: Boolean,
      default: false,
    },
    showButton: {
      type: Boolean,
      default: false,
    },
    propsData: {
      type: Object,
      default: () => ({}),
    },
    useTipClass: {
      type: Boolean,
      default: false,
    },
    hideTipStyle: {
      type: Boolean,
      default: false,
    },
  },
  emits: [
    'update:show',
  ],
  data() {
    return {};
  },
  watch: {
  },
  methods: {
    closeDialog() {
      this.$emit('update:show', false);
    },
    getActClass(...args) {
      return getActClass(this.useTipClass, args);
    },
  },
};
